<template>
	<div class="diy_edit page_redemption_records" id="redemption_records_edit">
		<div class='warp'>
			<div class='container'>
				<div class='row diy_edit_content_box'>
						<div v-if="$check_field('set','exchange_number') || $check_field('add','exchange_number') || $check_field('get','exchange_number')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								兑换号:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_exchange_number" v-model="form['exchange_number']" placeholder="请输入兑换号" v-if="(form['exchange_number'] && $check_field('set','exchange_number')) || (!form['exchange_number'] && $check_field('add','exchange_number'))" :disabled="true"/>
							<span v-else-if="$check_field('get','exchange_number')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','product_number') || $check_field('add','product_number') || $check_field('get','product_number')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								商品编号:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_product_number" v-model="form['product_number']" placeholder="请输入商品编号" v-if="(form['product_number'] && $check_field('set','product_number')) || (!form['product_number'] && $check_field('add','product_number'))"  :disabled="disabledObj['product_number_isDisabled']"/>
							<span v-else-if="$check_field('get','product_number')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','product_name') || $check_field('add','product_name') || $check_field('get','product_name')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								商品名称:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_product_name" v-model="form['product_name']" placeholder="请输入商品名称" v-if="(form['product_name'] && $check_field('set','product_name')) || (!form['product_name'] && $check_field('add','product_name'))"  :disabled="disabledObj['product_name_isDisabled']"/>
							<span v-else-if="$check_field('get','product_name')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','product_type') || $check_field('add','product_type') || $check_field('get','product_type')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								商品类型:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_product_type" v-model="form['product_type']" placeholder="请输入商品类型" v-if="(form['product_type'] && $check_field('set','product_type')) || (!form['product_type'] && $check_field('add','product_type'))"  :disabled="disabledObj['product_type_isDisabled']"/>
							<span v-else-if="$check_field('get','product_type')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','product_specifications') || $check_field('add','product_specifications') || $check_field('get','product_specifications')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								商品规格:
							</span>
						</div>
								<!-- 文本 -->
									<div class="diy_field diy_text">
							<input type="text" id="form_product_specifications" v-model="form['product_specifications']" placeholder="请输入商品规格" v-if="(form['product_specifications'] && $check_field('set','product_specifications')) || (!form['product_specifications'] && $check_field('add','product_specifications'))"  :disabled="disabledObj['product_specifications_isDisabled']"/>
							<span v-else-if="$check_field('get','product_specifications')">{{ form['${obj.name}'] }}</span>
						</div>
										</div>
							<div v-if="$check_field('set','required_points') || $check_field('add','required_points') || $check_field('get','required_points')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								所需积分:
							</span>
						</div>
								<!-- 数字 -->
						<div class="diy_field diy_number">
							<input type="number" id="form_required_points" v-model.number="form['required_points']" placeholder="请输入所需积分" v-if="(form['required_points'] && $check_field('set','required_points')) || (!form['required_points'] && $check_field('add','required_points'))" :disabled="disabledObj['required_points_isDisabled']" />
							<span v-else-if="$check_field('get','required_points')">{{ form['${obj.name}'] }}</span>
						</div>
							</div>
							<div v-if="$check_field('set','redemption_date') || $check_field('add','redemption_date') || $check_field('get','redemption_date')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								兑换日期:
							</span>
						</div>
								<!-- 日期 -->
						<div class="diy_field diy_date">
							<input type="date" :disabled="disabledObj['redemption_date_isDisabled']" id="form_redemption_date" v-model="form['redemption_date']" placeholder="请输入兑换日期" v-if="(form['redemption_date'] && $check_field('set','redemption_date')) || (!form['redemption_date'] && $check_field('add','redemption_date'))" />
							<span v-else-if="$check_field('get','redemption_date')">{{ form['${obj.name}'] }}</span>
						</div>
							</div>
							<div v-if="$check_field('set','ordinary_users') || $check_field('add','ordinary_users') || $check_field('get','ordinary_users')" class="form-item col-12 col-md-6">
						<div class="diy_title">
							<span>
								普通用户:
							</span>
						</div>
						<div class="diy_field diy_down">
							<select id="form_ordinary_users" :disabled="disabledObj['ordinary_users_isDisabled']" v-model="form['ordinary_users']" v-if="(form['ordinary_users'] && $check_field('set','ordinary_users')) || (!form['ordinary_users'] && $check_field('add','ordinary_users'))" >
								<option v-for="o in list_user_ordinary_users" :value="o['user_id']">
									{{o['nickname'] + '-' + o['username']}}
								</option>
							</select>
							<span v-else-if="$check_field('get','ordinary_users')">{{ form['ordinary_users'] }}</span>
						</div>
					</div>
	




				</div>
				<div class="diy_edit_submit_box row">
					<div class="col-12">
						<div class="btn_box">
							<button class="btn_submit" @click="submit()">提交</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import mixin from "@/mixins/page.js";
	export default {
		mixins: [mixin],
		components: {},
		data() {
			return {
				url_get_obj: "~/api/redemption_records/get_obj?",
				url_add: "~/api/redemption_records/add?",
				url_set: "~/api/redemption_records/set?",

				// 登录权限
				oauth: {
					"signIn": true,
					"user_group": []
				},

				// 查询条件
				query: {
						"exchange_number": "",
							"product_number": "",
							"product_name": "",
							"product_type": "",
							"product_specifications": "",
							"required_points": 0,
							"redemption_date": "",
							"ordinary_users": 0,
						"redemption_records_id": 0,
				},

				obj: {
						"exchange_number": this.$get_stamp(), // 兑换号
							"product_number":  '', // 商品编号
							"product_name":  '', // 商品名称
							"product_type":  '', // 商品类型
							"product_specifications":  '', // 商品规格
							"required_points":  0, // 所需积分
							"redemption_date": new Date().getTime(),
							"ordinary_users": 0, // 普通用户
						"redemption_records_id": 0,
          			"user_id": this.$store.state.user.user_id,
				},

				// 表单字段
				form: {
						"exchange_number": this.$get_stamp(), // 兑换号
							"product_number":  '', // 商品编号
							"product_name":  '', // 商品名称
							"product_type":  '', // 商品类型
							"product_specifications":  '', // 商品规格
							"required_points":  0, // 所需积分
							"redemption_date": new Date().getTime(),
							"ordinary_users": 0, // 普通用户
						"redemption_records_id": 0,
          			"user_id": this.$store.state.user.user_id,
				},
				disabledObj:{
						"exchange_number_isDisabled": false,
							"product_number_isDisabled": false,
							"product_name_isDisabled": false,
							"product_type_isDisabled": false,
							"product_specifications_isDisabled": false,
									"redemption_date_isDisabled": false,
							"ordinary_users_isDisabled": false,
					},

																				// 用户列表
				list_user_ordinary_users: [],
	
				// ID字段
				field: "redemption_records_id",

        limit_times: 0, // 限制次数
        limit_type:0,
        act_limit_times:0,
			}
		},
		methods: {
      /**
       * 提交前验证事件
       * @param {Object} 请求参数
       * @return {String} 验证成功返回null, 失败返回错误提示
       */
      submit_check(param) {
        if(this.act_limit_times>=this.limit_times){
          return "已超过提交次数";
        }
        return null;
      },

      check_limit(){
        /**
         * 提交前验证事件
         * @param {Object} 请求参数
         * @return {String} 验证成功返回null, 失败返回错误提示
         */
        let _this = this;
        if (_this.$store.state.user.user_group==='管理员'){
          _this.limit_times = 999;
        }else {
          _this.$get("~/api/point_products/get_obj?",
              {"product_number":_this.form.product_number}, function(res) {
            if (res.result && res.result.obj) {
              _this.limit_times=res.result.obj.limit_times;
              _this.limit_type=res.result.obj.limit_type;
              let user_id = _this.$store.state.user.user_id;
              let limit_url = "~/api/redemption_records/count?product_number="+_this.form.product_number+"&user_id="+user_id;
              if (_this.limit_type === 1){
                let day = _this.$toTime(parseInt((new Date()).getTime()), "yyyy-MM-dd");
                let time = "&create_time_min="+day+" 00:00:00&create_time_max="+day+" 23:59:59"
                limit_url = limit_url +time;
              }
              _this.$get(limit_url ,{}, function(res) {
                _this.act_limit_times = res.result;
              });
            }
          });
        }
      },
			
				
				
				
				
				
				
						/**
			 * 获取普通用户用户列表
			 */
			async get_list_user_ordinary_users() {
				// if(this.user_group !== "管理员" && this.form["ordinary_users"] === 0) {
				//     this.form["ordinary_users"] = this.user.user_id;
				// }
				var json = await this.$get("~/api/user/get_list?user_group=普通用户");
				if(json.result && json.result.list){
					this.list_user_ordinary_users = json.result.list;
				}
				else if(json.error){
					console.error(json.error);
				}
			},
					async get_user_session_ordinary_users(){
				var _this = this;
				var json = await this.$get("~/api/user_group/get_obj?name=普通用户");
				if(json.result && json.result.obj){
					var source_table = json.result.obj.source_table;
					var user_id = _this.$store.state.user.user_id;
					if (user_id){
						var url = "~/api/"+source_table+"/get_obj?"
						this.$get(url, {"user_id":_this.$store.state.user.user_id}, function(res) {
							if (res.result && res.result.obj) {
								var arr = []
								for (let key in res.result.obj) {
									arr.push(key)
								}
								var arrForm = []
								for (let key in _this.form) {
									arrForm.push(key)
								}
								_this.form["ordinary_users"] = user_id
								_this.disabledObj['ordinary_users' + '_isDisabled'] = true
								for (var i=0;i<arr.length;i++){
                  if (arr[i]!=='examine_state' && arr[i]!=='examine_reply') {
                    for (var j = 0; j < arrForm.length; j++) {
                      if (arr[i] === arrForm[j]) {
                        if (arr[i] !== "ordinary_users") {
                          _this.form[arrForm[j]] = res.result.obj[arr[i]]
                          _this.disabledObj[arrForm[j] + '_isDisabled'] = true
                          break;
                        }
                      }
                    }
                  }
								}
							}
						});
					}
				}
				else if(json.error){
					console.error(json.error);
				}
			},
	
	
			/**
			 * 修改文件
			 * @param { Object } files 上传文件对象
			 * @param { String } str 表单的属性名
			 */
			change_file(files, str) {
				var form = new FormData();
				form.append("file", files[0]);
				this.$post("~/api/redemption_records/upload?", form, (res) => {
					if (res.result) {
						this.form[str] = res.result.url;
					} else if (res.error) {
						this.$toast(res.error.message);
					}
				});
			},

			/**
			 * 获取对象后获取缓存表单
			 * @param {Object} json
			 * @param {Object} func
			 */
			get_obj_before(param){
				var form = $.db.get("form");
				// if (form) {
        //   delete(form.examine_state)
        //   delete(form.examine_reply)
        //   this.obj = $.push(this.obj ,form);
				// 	this.form = $.push(this.form ,form);
				// }
				// var arr = []
				// for (let key in form) {
				// 	arr.push(key)
				// }
				// for (var i=0;i<arr.length;i++){
				// 	this.disabledObj[arr[i] + '_isDisabled'] = true
				// }
        if (form) {
          var arr = []
          for (let key in form) {
            arr.push(key)
          }
          var arrForm = []
          for (let key in this.form) {
            arrForm.push(key)
          }
          for (var i=0;i<arr.length;i++){
            if (arr[i]!=='examine_state' && arr[i]!=='examine_reply') {
              for (var j = 0; j < arrForm.length; j++) {
                if (arrForm[j] === arr[i]) {
                  this.form[arrForm[j]] = form[arr[i]]
                  this.obj[arrForm[j]] = form[arr[i]]
                  this.disabledObj[arrForm[j] + '_isDisabled'] = true
                  break;
                }
              }
            }
          }
        }
														        if (JSON.stringify(this.form["redemption_date"]).indexOf("-")===-1) {
          this.form["redemption_date"] = this.$toTime(parseInt(this.form["redemption_date"]), "yyyy-MM-dd")
        }
			
        $.db.del("form");
				return param;
			},

			/**
			 * 获取对象后获取缓存表单
			 * @param {Object} json
			 * @param {Object} func
			 */
			get_obj_after(json ,func){
				// var form = $.db.get("form");
				// var obj = Object.assign({} ,form ,this.obj);
				// if (obj) {
        //   delete(obj.examine_state)
        //   delete(obj.examine_reply)
				// 	this.obj = $.push(this.obj ,obj);
				// }
				// if (form) {
        //   delete(form.examine_state)
        //   delete(form.examine_reply)
				// 	this.form = $.push(this.form ,form);
				// }
				if(func){
					func(json);
				}
			},

		},
		created() {
																										this.get_user_session_ordinary_users();
					this.get_list_user_ordinary_users();
			},
    watch: {
      'form.product_number': {
        handler: function() {
          this.check_limit();
        }
      }
    }
	}
</script>

<style>




</style>
